@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@100;200;500;700&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.4.0/font/bootstrap-icons.css);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Yanone Kaffeesatz", sans-serif;
  text-decoration: none;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(https://venngage-wordpress.s3.amazonaws.com/uploads/2018/11/15-Presentation-Background-Examples12.jpeg)
    no-repeat center center fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}




/* Name properties */
h1 {
    font-size: larger;
    padding: 10px;
    /* color: rgb(240, 206, 170); */
    color: rgb(228, 215, 255);
    text-shadow: 4px 4px 4px rgb(19, 16, 16), -1px 1px 2px #000, 1px -1px 1px #000,
      -1px -1px 1px #000;
      font-family: 'Yanone Kaffeesatz', sans-serif;
  }

  /* Job Title settings */
.logo {
  font-size: 40px;
  padding: 10px;
  color: rgb(223, 208, 240);
  text-shadow: 4px 4px 4px black, -1px 1px 2px #000, 1px -1px 1px #000,
    -1px -1px 1px #000;
  margin-left: 7%;
}
/* sets different style between subheading */
.subtitle {
  font-size: 40px;
  padding: 10px;
  color: rgb(255, 240, 203);
  /* color: rgba(136, 213, 145, 0.803); */
  text-shadow: 4px 4px 4px black, -1px 1px 2px #000, 1px -1px 1px #000,
    -1px -1px 1px rgb(223, 208, 240);
}
footer {
    position: -webkit-sticky;
    position: sticky;
    text-align: center;
    font-size: 2rem;
  }

  .icons-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 10px;
    gap: 10px;
    max-width: 300px;
    margin: auto;
    -webkit-align-items: center;
            align-items: center;
    /* position: center; */
  }

  .link-item:hover {
    border-color: rgb(240, 206, 170);
    border-color: rgb(223, 208, 240);
    border-style: solid;
    transition: all 0.3s ease 0s !important;
    border-radius: 5px;
    /* padding: 10px; */
  }

  
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

